.header {
    height: 852px;
    padding-top: 20px;
    background-image: url('../img/bg_section-1.jpg');
    background-position: center;
    background-size: cover;
    position: relative;
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__descr {
        width: 360px;
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 160%;
        color: #FFFFFF;
        span {
            font-weight: 600;
        }
    }
    &__contact {
        width: 360px;
        text-align: right;
        line-height: 160%;
        position: relative;
        &::after {
            content: url('../img/icons/phone_green.svg');
            position: absolute;
            left: 175px;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    &__phone {
        font-weight: 600;
        font-size: 18px;
        color: #FFFFFF;
        text-decoration: none;
    }
    &__text {
        font-weight: 400;
        font-size: 16px;
        color: #FFFFFF;
        padding-right: 10px;
    }
    &__title {
        font-weight: 700;
        font-size: 47px;
        line-height: 130%;
        text-align: center;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #FFFFFF;
        margin-top: 36px;
        span {
            background-image: linear-gradient(90.24deg, #C39D65 -53.59%, #D5AF76 -7.12%, #EBC389 20.47%, #CFA870 58.94%, #C39D65 85.81%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
        }
    }
    &__small-title {
        font-weight: 400;
        font-size: 22px;
        line-height: 130%;
        text-align: center;
        color: #FFFFFF;
        border: 1px solid #414143;
        border-radius: 5px;
        padding: 12px;
        margin-top: 15px;
        span {
            font-weight: 600;
            background: linear-gradient(90.24deg, #C39D65 -53.59%, #D5AF76 -7.12%, #EBC389 20.47%, #CFA870 58.94%, #C39D65 85.81%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
        }
    }
    &__franchise {
        width: 822px;
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        text-align: center;
        color: #FFFFFF;
        margin: 75px auto 0;
        span {
            font-weight: 600;
        }
    }
}

.advantages {
    display: flex;
    justify-content: space-between;
    width: 900px;
    margin: 35px auto 0;
    &__item {
        width: 221px;
        position: relative;
        &::after {
            content: url('../img/icons/line.svg');
            position: absolute;
            top: 0;
            right: -55%;
            transform: translateX(-55%);
        }
        &:last-child::after {
            content: '';
        }
        &-img {
            width: 56px;
            height: 56px;
            margin: 0 auto;
        }
        &-descr {
            margin-top: 13px;
            font-weight: 400;
            font-size: 16px;
            line-height: 160%;
            text-align: center;
            color: #FFFFFF;
        }
    }
}

.form {
    max-width: 1140px;
    height: 227px;
    background-color: #FFFFFF;
    padding: 0 19px 0 19px;
    border-radius: 10px;
    box-shadow: 0px 100px 199px rgba(82, 82, 82, 0.07), 0px 41.7776px 83.1374px rgba(82, 82, 82, 0.0503198), 0px 22.3363px 44.4493px rgba(82, 82, 82, 0.0417275), 0px 12.5216px 24.9179px rgba(82, 82, 82, 0.035), 0px 6.6501px 13.2337px rgba(82, 82, 82, 0.0282725), 0px 2.76726px 5.50684px rgba(82, 82, 82, 0.0196802);
    position: absolute;
    bottom: -113px;
    &__title {
        font-weight: 600;
        font-size: 18px;
        line-height: 160%;
        text-align: center;
        color: #2E2E2F;
    }
    &__wrapper {
        margin-top: 16px;
        padding: 22px 10px 24px 10px;
    }
    &__input {
        width: 235px;
        height: 80px;
        background-color: #F4F4F4;
        border-radius: 5px;
        border: none;
        padding: 26px 5px 28px 38px;
        &::placeholder {
            font-weight: 400;
            font-size: 16px;
            line-height: 160%;
            color: #AEAEAF;
        }
        &_phone {
            text-transform: uppercase;
            background-image: url('../img/icons/form_phone.svg');
            background-repeat: no-repeat;
            background-position: 15px;
        }
        &_mail {
            background-image: url('../img/icons/form_mail.svg');
            background-repeat: no-repeat;
            background-position: 15px;
        }
        &_name {
            background-image: url('../img/icons/form_people.svg');
            background-repeat: no-repeat;
            background-position: 15px;
        }
    }
    &__btn {
        width: 345px;
        height: 94px;
        background: linear-gradient(85.08deg, #569434 -11.21%, #6AAB46 15.54%, #77BA53 41.42%, #7BBE57 61.67%, #91D36C 86.91%, #A4E67E 100.4%);
        border-radius: 5px;
        border: none;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        line-height: 160%;
        letter-spacing: 0.1em;
        color: #FFFFFF;
        position: relative;
        &::after {
            content: url('../img/icons/icon_press-button.svg');
            position: absolute;
            right: 30px;
        }
    }
    &__checkbox {
        position: absolute;
        opacity: 0;
    }
    &__checkbox + label::before {
        content: "";
        width: 12px;
        height: 12px;
        background-color: #AEAEAF;
        display: inline-flex;
        align-items: flex-end;
        margin-right: 5px;
        border-radius: 3px;
    }

    &__checkbox:checked+label::before {
        border-color: #AEAEAF;
        background-color: #FFFFFF;
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12 12'%3e%3cpath fill='%23AEAEAF' d='M0.88403 0.0215119C0.468068 0.0914656 0.0928871 0.475249 0.0229742 0.902332C-0.00765808 1.08934 -0.00765808 10.9077 0.0229742 11.0947C0.0947152 11.533 0.46692 11.9053 0.905077 11.977C1.09203 12.0077 10.908 12.0077 11.0949 11.977C11.5331 11.9053 11.9053 11.533 11.977 11.0947C12.0077 10.9077 12.0077 1.08934 11.977 0.902332C11.9053 0.464067 11.5331 0.0917703 11.0949 0.0200116C10.9262 -0.00762761 1.04868 -0.00617415 0.88403 0.0215119ZM9.55128 3.03482C9.59415 3.05517 9.73023 3.17592 9.85365 3.30314L10.0781 3.53448V3.65833V3.78218L7.68161 6.18182C6.36356 7.50163 5.24298 8.60947 5.19142 8.64372C5.10958 8.69808 5.07386 8.70598 4.91017 8.70608C4.75744 8.70615 4.70747 8.69663 4.64065 8.65464C4.59553 8.62628 3.96538 8.00875 3.24028 7.28234L1.92194 5.96157V5.85462C1.92194 5.77384 1.93835 5.72531 1.98907 5.65625C2.08971 5.51918 2.39851 5.22968 2.4752 5.20052C2.64137 5.13732 2.6289 5.12698 3.80894 6.3048L4.91078 7.40457L7.07815 5.23657C8.27019 4.0442 9.27726 3.05291 9.31609 3.03374C9.40675 2.98899 9.45517 2.9892 9.55128 3.03482Z'/%3e%3c/svg%3e");
    }

    &__text {
        font-weight: 400;
        font-size: 14px;
        line-height: 160%;
        color: #AEAEAF;
    }
    &__policy {
        color: #AEAEAF;
        text-decoration: underline;
    }
}
