.tasks {
    height: 1311px;
    padding-top: 220px;
    background-image: url('../img/tasks_bg.jpg');
    background-position: center;
    background-size: cover;
    &__title {
        width: 800px;
        font-weight: 700;
        font-size: 40px;
        line-height: 130%;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #2E2E2F;
        margin-top: 6px;
        span {
            color: #D65B48;
        }
    }
    &__descr {
        width: 900px;
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        color: #2D2D2D;
        margin-top: 20px;
        span {
            font-weight: 600;
        }
    }
    &__partners {
        margin-top: 35px;
        &-title {
            width: 245px;
            height: 53px;
            background: linear-gradient(90.24deg, #C39D65 -53.59%, #D5AF76 -7.12%, #EBC389 20.47%, #CFA870 58.94%, #C39D65 85.81%);
            font-weight: 600;
            font-size: 18px;
            line-height: 160%;
            color: #FFFFFF;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 5px 5px 0 0;
            margin-left: 19px;
        }
        &-wrapper {
            background: linear-gradient(113.64deg, #4E4E4E 14.16%, #2E2E2F 76.67%);
            box-shadow: inset 1px -1px 1px rgba(255, 255, 255, 0.25), inset -1px 0px 1px rgba(255, 255, 255, 0.25);
            border-radius: 10px;
            width: 750px;
            height: 384px;
            padding: 10px;
            position: relative;
        }
        &-items {
            display: grid;
            grid-template: repeat(2, 1fr)/repeat(2, 1fr);
            gap: 1px;
            border: 1px solid #414143;
            border-radius: 10px;
        }
        &-item {
            color: #FFFFFF;
            font-weight: 400;
            font-size: 18px;
            line-height: 160%;
            padding: 23px 0 36px 43px;
            position: relative;
            span {
                font-weight: 600;
                line-height: 200%;
            }
            &::before {
                content: url('../img/icons/tasks_item.svg');
                position: absolute;
                left: 20px;
            }

        }
        &-descr {
            margin: 32px auto 0;
            width: 621px;
            font-weight: 600;
            font-size: 18px;
            line-height: 160%;
            text-align: center;
            color: #FFFFFF;
        }
    }
    &__circle {
        width: 240px;
        height: 240px;
        background: linear-gradient(135deg, rgb(255, 254, 250), rgb(246, 241, 227), rgb(195, 157, 101)) 50;
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: -267px;
        bottom: -47px;
    }
    &__circle-text {
        width: calc(100% - 50px);
        height: calc(100% - 50px);
        border-radius: 100%;
        background: linear-gradient(136.66deg, #FFFEFA 19.49%, #F6F1E3 66.6%);
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-weight: 400;
        font-size: 16px;
        line-height: 160%;
        color: #2E2E2F;
        span {
            font-weight: 600;
            font-size: 22px;
        }
    }
    &__hand {
        margin-top: 47px;
        width: 750px;
        padding: 23px 94px 26px 167px;
        position: relative;
        border: 1px solid #C6C6C6;
        border-radius: 10px;
        &-text {
            font-weight: 400;
            font-size: 16px;
            line-height: 160%;
            color: #2D2D2D;
            &_bold {
                font-weight: 600;
                font-size: 18px;
            }
            &_medium {
                span {
                    font-weight: 600;
                }
            }
        }
        &-img {
            position: absolute;
            top: -7px;
            left: -2px;
        }
    }
}

.border-bottom {
    border-bottom: 1px solid #414143;
}
.border-right {
    border-right: 1px solid #414143;
}
