.wrapper-modal {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.42);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    overflow: auto;
    display: none;
}
.active {
    display: flex;
}

.modal {
    width: 780px;
    padding: 30px 40px 0 0;
    min-height: 537px;
    margin: 101px 0 50px 0;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #FFFFFF;
    position: relative;
    &__left {
        &-title {
            font-weight: 600;
            font-size: 22px;
            line-height: 160%;
            color: #2E2E2F;
            padding-left: 40px;
        }
        &-img {
            margin-top: 55px;
            height: 347px;
            background-image: url('../img/modal_hand_map.jpg');
            background-position: -145px;
            border-radius: 10px;
        }
    }
    &__right {
        max-width: 344px;
        &-wrapper {
            display: flex;
            flex-direction: column;
        }
        &_input {
            width: 320px;
            margin-bottom: 10px;
        }
        &_btn {
            width: 320px;
            margin-bottom: 5px;
        }
        &_text {
            p {
                display: inline-block;
                padding-left: 17px;
                margin-top: -22px;
                font-weight: 400;
                font-size: 14px;
                line-height: 160%;
            }

        }
    }
    &__close {
        background: none;
        position: absolute;
        right: 0;
        top: -40px;
        border: none;
        cursor: pointer;
    }
}


.input-error {
    border: 1px solid red;
}
