.trend {
    background-image: url('../img/trend_bg.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 1344px;
    padding: 215px 0 108px;
    &__title {
        width: 835px;
        font-weight: 700;
        font-size: 40px;
        line-height: 130%;
        text-align: center;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #2D2D2D;
        margin: 0 auto;
        span {
            color: #E74E3E;
        }
    }
    &__descr {
        width: 976px;
        margin: 20px auto 0;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        text-align: center;
        color: #2E2E2F;
        span {
            font-weight: 600;
        }
    }
}

.trends-table {
    &__title {
        width: 220px;
        background: linear-gradient(113.64deg, #4E4E4E 14.16%, #2E2E2F 76.67%);
        border-radius: 5px;
        padding: 12px 30px;
        margin: 35px auto 0;
        font-weight: 600;
        font-size: 18px;
        line-height: 160%;
        text-align: center;
        color: #FFFFFF;
    }
    &__wrapper {
        margin-top: 30px;
        display: grid;
        grid-template: repeat(2, 312px) / repeat(3, 360px);
        gap: 30px;
    }
    &__item {
        width: 360px;
        height: 312px;
        border-radius: 10px;
        &-title {
            font-weight: 600;
            font-size: 22px;
            line-height: 130%;
            color: #2E2E2F;
            &_white {
                color: #FFFFFF;
                text-align: center;
            }
        }
        &-descr {
            margin-top: 11px;
            font-weight: 400;
            font-size: 16px;
            line-height: 160%;
            color: #2E2E2F;
            &_white {
                color: #FFFFFF;
                text-align: center;
            }
        }
        &_long {
            background-image: url('../img/trends_item_5bglong.jpg');
            background-position: center;
            background-size: cover;
            height: 100%;
            padding: 64px 13px 13px;
        }
        &_01 {
            background-image: url('../img/trends_item_1bg.jpg');
            background-position: center;
            background-size: cover;
            padding: 64px 133px 101px 30px;
        }
        &_02 {
            background-image: url('../img/trends_item_2bg.jpg');
            background-position: center;
            background-size: cover;
            padding: 64px 18px 52px 134px;
        }
        &_03 {
            background-image: url('../img/trends_item_3bg.jpg');
            background-position: center;
            background-size: cover;
            padding: 64px 58px 104px 154px;
        }
        &_04 {
            background-image: url('../img/trends_item_4bg.jpg');
            background-position: center;
            background-size: cover;
            padding: 64px 134px 75px 30px;
        }
        &_long {
            grid-column-start: 2;
            grid-column-end: 3;
            grid-row-start: 1;
            grid-row-end: 3;
        }
    }
    &__descr {
        margin: 31px auto 0;
        width: 1060px;
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        text-align: center;
        color: #2E2E2F;
        span {
            font-weight: 600;
        }
    }
}
