.gourmet {
    height: 915px;
    background-image: url('../img/Gourmet_bg.jpg');
    background-position: center;
    background-size: cover;
    padding: 101px 0 149px;
    position: relative;
    .container {
        position: relative;
    }
    &__title {
        width: 706px;
        font-weight: 700;
        font-size: 40px;
        line-height: 130%;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #FFFFFF;
        span {
            color: #C39D65;
        }
    }
    &__label {
        width: 362px;
        border: 1px dashed #F4F4F4;
        border-radius: 5px;
        font-weight: 600;
        font-size: 18px;
        line-height: 160%;
        color: #FFFFFF;
        padding: 13px 56px 17px 35px;
        position: absolute;
        top: 11px;
        right: 0;
        &::before {
            content: url('../img/icons/gourmet_i.png');
            background: linear-gradient(90.24deg, #C39D65 -53.59%, #D5AF76 -7.12%, #EBC389 20.47%, #CFA870 58.94%, #C39D65 85.81%);
            padding: 7px 12px;
            border-radius: 100%;
            position: absolute;
            left: -19px;
            bottom: 25px;
        }
    }
    &__descr {
        font-weight: 400;
        font-size: 18px;
        line-height: 160%;
        text-align: center;
        color: #FFFFFF;
        width: 1000px;
        margin: 20px auto 0;
    }
}

.tabs-box {
    width: 1140px;
    height: 388px;
    background-color: #FFFFFF;
    border-radius: 10px;
    &__tabs {
        height: 106px;
        display: flex;
    }
    &__tab {
        width: 228px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0 35px;
        text-align: center;
        font-weight: 400;
        font-size: 16px;
        line-height: 160%;
        color: #2E2E2F;
        border-left: 0.5px solid #AEAEAF;
        border-right: 0.5px solid #AEAEAF;
        border-bottom: 1px solid #AEAEAF;
        &_active {
            background: linear-gradient(90.24deg, #C39D65 -53.59%, #D5AF76 -7.12%, #EBC389 20.47%, #CFA870 58.94%, #C39D65 85.81%);
            color: #FFFFFF;
            font-weight: 600;
        }
    }
}
