@media (max-width: 992px) {
    .header {
        &__wrapper {
            padding: 0 10px;
        }
    }
    .form {
        padding: 0 10px;
        margin: 0 10px;
        &__input {
            width: 220px;
            &::placeholder {
                font-size: 14px;
            }
        }
        &__wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &__btn {
            width: 250px;
            &::after {
                content: '';
            }
        }
    }
    .tasks {
        padding: 220px 10px 0;
    }
    .history {
        padding: 101px 10px 62px;
    }
    .modal {
        width: 50%;
        display: block;
        text-align: center;
        padding: 15px;
        &__left {
            &-title {
                padding: 0 20px;
            }
            &-img {
                display: none;
            }
        }
        &__right {
            width: 300px;
            margin: 0 auto;
            &-wrapper {
                justify-content: space-between;
                align-items: center;
            }
            &_input {
                margin: 0 auto 5px;
            }
            &_text {
                display: block;
                margin-top: 10px;
                &-p {
                    padding-top: 15px;
                }
            }
        }
    }
}


@media (max-width: 768px) {
    .header {
        &__descr {
            width: 250px;
        }
        &__contact {
            width: 250px;
            &::after {
                left: 75px;
            }
        }
        &__franchise {
            width: 100%;
        }
    }
    .advantages {
        width: 100%;
        padding: 0 10px;
        margin: 62px auto 0;
    }
    .form {
        display: none;
    }
    .tasks {
        height: auto;
        padding: 70px 10px 0;
        background-size: contain;
        &__title {
            width: 100%;
        }
        &__descr {
            width: 100%;
        }
        &__circle {
            right: 48px;
            bottom: -257px;
        }
        &__hand {
            width: 50%;
            padding: 23px 10px 26px 20px;
            &-img {
                display: none;
            }
        }
    }
    .history {
        height: auto;
        padding: 70px 10px 15px;
        &__left {
            margin-right: 10px;
        }
        &__right {
            &-item {
                font-size: 14px;
            }
        }
    }
}

@media (max-width: 600px) {
    .container {
        width: 300px;
    }
    .header {
        height: auto;
        &__descr {
            display: none;
        }
        &__contact {
            &::after {
                left: 30px;
            }
        }
        &__title {
            font-size: 26px;
        }
        &__small-title {
            font-size: 14px;
        }
        &__franchise {
            font-size: 14px;
            margin: 40px auto 0;
        }
    }
    .advantages {
        display: block;
        padding: 10px;
        margin: 40px auto 0;
        &__item {
            margin: 0 auto;
            &::after {
                content: '';
                display: none;
            }
        }
    }
    .tasks {
        background-image: none;
        &__title {
            font-size: 26px;
        }
        &__descr {
            font-size: 14px;
        }
        &__partners {
            &-wrapper {
                width: 100%;
                height: auto;
            }
            &-items {
                display: block;
            }
            &-item {
                font-size: 16px;
                padding: 15px 0 15px 43px;
            }
            &-descr {
                width: 100%;
                font-size: 14px;
            }
        }
        &__circle {
            position: unset;
            margin: 0 auto;
        }
        &__hand {
            width: 100%;
            margin-bottom: 15px;
        }
    }
    .history {
        height: 1700px;
        &__title {
            font-size: 26px;
        }
        &__wrapper {
            display: block;
        }
        &__left {
            &-maksim {
                margin-top: 15px;
            }
            &-descr {
                width: 100%;
            }
        }
        &__right {
            &-title {
                margin-top: 30px;
            }
            margin-top: 15px;
            max-height: none;
        }
    }
    .modal {
        width: 90%;
    }

}
