.history {
    height: 1066px;
    padding: 101px 0 62px 0;
    background-image: url('../img/history_bg.jpg');
    background-position: center;
    background-size: cover;
    position: relative;
    &__title {
        max-width: 751px;
        font-weight: 700;
        font-size: 40px;
        line-height: 130%;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: #FFFFFF;
        span {
            color: #C39D65;
        }
    }
    &__wrapper {
        height: 686px;
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
    }
    &__left {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        &-start {
            max-width: 468px;
            font-weight: 400;
            font-size: 18px;
            line-height: 160%;
            color: #FFFFFF;
        }
        &-maksim {
            width: 281px;
            padding: 13px 15px 15px 20px;
            border-radius: 5px 5px 0 0;
            background: linear-gradient(113.64deg, #4E4E4E 14.16%, #2E2E2F 76.67%);
            font-weight: 600;
            font-size: 16px;
            line-height: 160%;
            color: #FFFFFF;
            span {
                font-weight: 400;
                font-size: 16px;
                color: #AEAEAF;
            }
        }
        &-descr {
            width: 380px;
            padding: 13px 20px 15px;
            border-radius: 0 5px 5px 5px;
            background: rgba(255, 255, 255, 0.7);
            font-weight: 400;
            font-size: 16px;
            line-height: 160%;
            color: #2E2E2F;
            span {
                font-weight: 600;
            }
        }
    }
    &__right {
        max-width: 555px;
        max-height: 686px;
        padding: 30px 30px 26px;
        background-color: #FFFFFF;
        border-radius: 5px;
        position: relative;
        &::before {
            content: url('../img/icons/history_flag.svg');
            position: absolute;
            right: 30px;
            top: -5px;
        }
        &-title {
            width: 255px;
            padding: 12px 30px;
            background: linear-gradient(90.24deg, #C39D65 -53.59%, #D5AF76 -7.12%, #EBC389 20.47%, #CFA870 58.94%, #C39D65 85.81%);
            border-radius: 5px;
            font-weight: 600;
            font-size: 18px;
            line-height: 160%;
            text-align: center;
            color: #FFFFFF;
        }
        &-items {
            margin-top: 5px;
        }
        &-item {
            margin-top: 17px;
            padding-left: 48px;
            font-weight: 400;
            font-size: 18px;
            line-height: 160%;
            color: #2E2E2F;
            position: relative;
            span {
                font-weight: 600;
            }
            &::before {
                content: url('../img/icons/history_items.svg');
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
}

.another {
    height: 300px;
}
